<template>
  <div id="app">
      <div id="allmap" style="width:1000px;height:400px;"></div>
      <!-- <div id="r-result">
          : <input id="cityName" type="text" style="width:100px; margin-right:10px;" />
          <input type="button" value="" id="btn" />
        </div> -->
    <div id="wrapper">
        <div id="left">
          <h2><h2>
            <p>800-0885-460</p>
            <p>www.qifeiye.com</p>
            <p>info@email.com</p>
            <p></p>
            </h2></h2>
            <p>
            <span>~</span><br>
            <span>10:00-14:0015:00-20:00</span><br></p>
            <p></p><span>~</span><br>
            <span>08:00-14:0015:00-19:00</span><br></p>
        </div>
        <div id="right">
          <p id="span">
              
          </p>
          <input id="name" placeholder=""><br>
          <input id='email' placeholder=""><br>
          <input id="phone" placeholder=""><br>
          <textarea placeholder="" style="resize:none" id="" cols="55" rows="3" ></textarea>
          <a href="#"><button></button></a>
        </div>
    </div>

  </div>
</template>

<script type="text/javascript">

	export default {
		mounted() {
			var map = new BMap.Map("allmap");
			var point = new BMap.Point(116.331398, 39.897445);
			map.centerAndZoom(point, 11);
// 			map.setCurrentCity("");
			function theLocation() {
				var city = document.getElementById("cityName").value;
				if(city != "") {
					map.centerAndZoom(city, 11); // 
				}
			}
		}
	}

</script>

<style>
  #wrapper{
    width:1000px;
    margin-left: 120px;
    margin-bottom: 50px;
    display: flex;
    flex-direction: row;
  }
  #left{
    width:500px;
    padding:50px;
    background: #f1f4f5;

  }
  h2{
    font-size: 22px;
    color:rgb(43, 175, 123);
    padding:10px 0;
    font-weight: bold;

  }
  p{
    color:#696969;
    font-size: 16px;
    margin:15px 0;
    font-weight: normal;
    
  }
  span{
    color:#696969;
    font-size: 16px;
    
  }
  #right{
    width:500px;
    background:#2baf7b;
    padding:40px;

  }
  #span{
    color:#fff;
    margin:30px 0;
  }
  input{
    width:427px;
    margin:10px 0 ;
    background: #2baf7b;
    border: 1px solid #fff;
    outline: none;
    padding:5px;
    
  }
  textarea{
    background: #2baf7b;
    border: 1px solid #fff;
    outline: none;    
    padding:5px;
  }
  button{
    background: #fff;
    color:#2baf7b;
    padding:5px 20px;
    border:none;
    outline: none;
    font-weight: bold;
    margin:20px 0;
  }
  input::-webkit-input-placeholder {
    color: #fff;
    opacity: 1;
  }
  textarea::-webkit-textarea-placeholder {
    color: #fff;
    opacity: 1;
  }
	 #allmap{ margin-left: 120px;} 
		/* #r-result{width:100%; font-size:14px;} */
</style>



